import { post, get } from '@/http/axios'
export default {
  namespaced: true,
  state: {
    // 产品分类数据
    addressData: {},
  },
  mutations: {
    // 产品分类数据
    SET_AddressData(state, addressData) {
      state.addressData = addressData
    }
  },
  actions: {
    // 获取地址列表
    async getAddressData({ commit }, params) {
      let res = await get('/address/pageQuery?page=1&pageSize=100')
      commit('SET_AddressData', res.data.data)
      // console.log(res.data.data.list);
    },
    // 新增地址
    async saveOrUpdateAddress({commit}, params){
      let res = await post('/address/saveOrUpdate', params)
      return res
    }
  }
}